// src/components/ItemList.tsx

import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchItems, updateItem, deleteItem } from '../redux/actions.ts';
import { RootState } from '../redux/rootReducer.ts';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { IconButton, Dialog, DialogContent, DialogTitle, Button } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import UpdateItemForm from './UpdateItemForm.tsx'; 
import 'react-quill/dist/quill.snow.css';

const ItemList: React.FC = () => {
  const dispatch = useDispatch();
  const [editItem, setEditItem] = useState(null); // Store item to edit
  const [openDialog, setOpenDialog] = useState(false); // Control modal visibility

  useEffect(() => {
    dispatch(fetchItems());
  }, [dispatch]);

  const { items, loading, error } = useSelector((state: RootState) => state.item);
  const [result, setResult] = useState(items);

  useEffect(() => {
    setResult(items);
  }, [items]);

  const handleChange = (e) => {
    const searchTerm = e.target.value.toLowerCase().trim();
    setResult(items.filter((quiz) => quiz.question.toLowerCase().includes(searchTerm)));
  };

  const handleEdit = (item) => {
    setEditItem(item);
    setOpenDialog(true);
  };

  const handleDelete = (id) => {
    if (window.confirm("Are you sure you want to delete this item?")) {
      dispatch(deleteItem(id));
    }
  };

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div>
      {/* Search Box */}
      <Box component="form" sx={{ '& > :not(style)': { m: 0, width: '100%' } }} noValidate autoComplete="off">
        <TextField id="outlined-basic" label="Search what you want" variant="outlined" onChange={handleChange} />
      </Box>

      {/* Item List */}
      <List sx={{ minWidth: '80%', bgcolor: 'background.paper', fontSize: '23px', fontFamily: 'Segoe UI' }} component="nav">
        {result.map((quiz, index) => (
          <div key={quiz._id}>
            <ListItemButton>
              <ListItemIcon>{index + 1}</ListItemIcon>
              <ListItemText>
                <b style={{ fontSize: '30px', fontFamily: 'Segoe UI' }}>{quiz.question}</b>
              </ListItemText>

              {/* Edit Button */}
              <IconButton onClick={() => handleEdit(quiz)} style={{ marginRight: '10px' }}>
                <EditIcon />
              </IconButton>

              {/* Delete Button */}
              <IconButton onClick={() => handleDelete(quiz._id)} style={{ marginRight: '20px' }}>
                <DeleteIcon />
              </IconButton>
            </ListItemButton>

            {/* Answer Section */}
            <List component="div" disablePadding>
              <ListItemButton sx={{ pl: 4 }}>
                <div dangerouslySetInnerHTML={{ __html: quiz.answer }} />
              </ListItemButton>
            </List>
          </div>
        ))}
      </List>

      {/* Update Item Modal */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} fullWidth>
        <DialogTitle>Edit Item</DialogTitle>
        <DialogContent>
          {editItem && <UpdateItemForm item={editItem} onClose={() => setOpenDialog(false)} />}
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default ItemList;
