// src/redux/actions.ts

import axios from 'axios';
import { Dispatch } from 'redux';
import {
  FETCH_ITEMS_REQUEST, FETCH_ITEMS_SUCCESS, FETCH_ITEMS_FAILURE,
  ADD_ITEM_REQUEST, ADD_ITEM_SUCCESS, ADD_ITEM_FAILURE,
  UPDATE_ITEM_REQUEST, UPDATE_ITEM_FAILURE, UPDATE_ITEM_SUCCESS,
  DELETE_ITEM_FAILURE, DELETE_ITEM_REQUEST, DELETE_ITEM_SUCCESS
} from './actionTypes.ts';

// Fetch Items Action
export const fetchItems:any = () => {
  return async (dispatch: Dispatch) => {
    dispatch({ type: FETCH_ITEMS_REQUEST });

    try {
      const response = await axios.get('/api');
      console.log("Here",response.data)
      dispatch({
        type: FETCH_ITEMS_SUCCESS,
        payload: response.data, // response data will be an array of items
      });
    } catch (error: any) {
      dispatch({
        type: FETCH_ITEMS_FAILURE,
        payload: error.message, // error message
      });
    }
  };
};

// Add Item Action
export const addItem:any  = (itemData: { question: string; answer: string }) => {
  return async (dispatch: Dispatch) => {
    dispatch({ type: ADD_ITEM_REQUEST });

    try {
      const response = await axios.post('/api', itemData);
      dispatch({
        type: ADD_ITEM_SUCCESS,
        payload: response.data, // response data will be the added item
      });
      history.back();
    } catch (error: any) {
      dispatch({
        type: ADD_ITEM_FAILURE,
        payload: error.message, // error message
      });
    }
  };
};
// Update Item Action
export const updateItem: any = (id: number, updatedData: { question: string; answer: string }) => {
  return async (dispatch: Dispatch) => {
    dispatch({ type: UPDATE_ITEM_REQUEST });

    try {
      const response = await axios.put(`/api/${id}`, updatedData);
      dispatch({
        type: UPDATE_ITEM_SUCCESS,
        payload: response.data.quiz, // updated item from server
      });
      console.log(response.data)
    } catch (error: any) {
      dispatch({
        type: UPDATE_ITEM_FAILURE,
        payload: error.message,
      });
    }
  };
};

// Delete Item Action
export const deleteItem: any = (id: number) => {
  return async (dispatch: Dispatch) => {
    dispatch({ type: DELETE_ITEM_REQUEST });

    try {
      await axios.delete(`/api/${id}`);
      dispatch({
        type: DELETE_ITEM_SUCCESS,
        payload: id, // send the deleted item id
      });
    } catch (error: any) {
      dispatch({
        type: DELETE_ITEM_FAILURE,
        payload: error.message,
      });
    }
  };
};
