// src/redux/reducer.ts

import {
  FETCH_ITEMS_REQUEST, FETCH_ITEMS_SUCCESS, FETCH_ITEMS_FAILURE,
  ADD_ITEM_REQUEST, ADD_ITEM_SUCCESS, ADD_ITEM_FAILURE,
  UPDATE_ITEM_REQUEST, UPDATE_ITEM_SUCCESS, UPDATE_ITEM_FAILURE,
  DELETE_ITEM_REQUEST, DELETE_ITEM_SUCCESS, DELETE_ITEM_FAILURE
} from './actionTypes.ts';

interface Item {
  _id: string;
  question: string;
  description: string;
}

interface ItemState {
  loading: boolean;
  items: Item[];
  error: string | null;
}

const initialState: ItemState = {
  loading: false,
  items: [],
  error: null,
};

const itemReducer = (state = initialState, action: any): ItemState => {
  switch (action.type) {
    case FETCH_ITEMS_REQUEST:
      return { ...state, loading: true };
    case FETCH_ITEMS_SUCCESS:
      return { ...state, loading: false, items: action.payload, error: null };
    case FETCH_ITEMS_FAILURE:
      return { ...state, loading: false, error: action.payload, items: [] };
    case ADD_ITEM_REQUEST:
      return { ...state, loading: true };
    case ADD_ITEM_SUCCESS:
      return {
        ...state,
        loading: false,
        items: [...state.items, action.payload], // Add the new item to the list
        error: null,
      };
    case ADD_ITEM_FAILURE:
      return { ...state, loading: false, error: action.payload };
    case UPDATE_ITEM_REQUEST:
      return { ...state, loading: true };
    case UPDATE_ITEM_SUCCESS:
      return {
        ...state,
        loading: false,
        items: state.items.map((item) =>
          item._id === action.payload._id ? action.payload : item
        ),
        error: null,
      };
    case UPDATE_ITEM_FAILURE:
      return { ...state, loading: false, error: action.payload };

    case DELETE_ITEM_REQUEST:
      return { ...state, loading: true };
    case DELETE_ITEM_SUCCESS:
      return {
        ...state,
        loading: false,
        items: state.items.filter((item) => item._id !== action.payload),
        error: null,
      };
    case DELETE_ITEM_FAILURE:
      return { ...state, loading: false, error: action.payload };
    default:
      return state;
  }
};

export default itemReducer;
