// src/components/UpdateItemForm.tsx

import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { updateItem } from '../redux/actions.ts';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import SendIcon from '@mui/icons-material/Send';
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

interface UpdateItemFormProps {
  item: { _id: string; question: string; answer: string };
  onClose: () => void; // Callback to close the form
}

const UpdateItemForm: React.FC<UpdateItemFormProps> = ({ item, onClose }) => {
  const [question, setQuestion] = useState(item.question);
  const [answer, setAnswer] = useState(item.answer);
  const dispatch = useDispatch();

  useEffect(() => {
    setAnswer(item.answer);
  }, [item]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(updateItem(item._id, { question, answer })); // Dispatch update action
    onClose(); // Close the form after updating
  };

  return (
    <div style={{ marginTop: '20px' }}>
      <Box
        component="form"
        sx={{ '& > :not(style)': { m: 0, width: '100%' }}}
        noValidate
        autoComplete="off"
      >
        <TextField
          id="outlined-basic"
          name="question"
          value={question}
          label="Question"
          variant="outlined"
          onChange={(e) => setQuestion(e.target.value)}
        />
        <br />
        <br />
        <ReactQuill
          value={answer}
          onChange={setAnswer}
          modules={{
            toolbar: [
              [{ header: '1' }, { header: '2' }, { font: [] }],
              [{ list: 'ordered' }, { list: 'bullet' }],
              ['bold', 'italic', 'underline'],
              [{ align: [] }],
              ['link'],
              [{ size: ['small', 'medium', 'large', 'huge'] }],
              [{ color: [] }, { background: [] }],
              ['blockquote', 'code-block'],
              ['clean'],
            ],
          }}
          formats={[
            'header',
            'font',
            'size',
            'bold',
            'italic',
            'underline',
            'list',
            'link',
            'align',
            'color',
            'background',
            'blockquote',
            'code-block',
          ]}
          style={{ height: '200px', display: "inline-block" }}
        />

        <Button variant="contained" type="submit" endIcon={<SendIcon />} onClick={handleSubmit} style={{marginBottom:'10px', marginTop: '10px'}}>
          Update
        </Button>
        <Button
          variant="outlined"
          onClick={onClose}
          sx={{ marginLeft: '10px'}}
        >
          Cancel
        </Button>
      </Box>
    </div>
  );
};

export default UpdateItemForm;
