import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { addItem } from '../redux/actions.ts';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import SendIcon from '@mui/icons-material/Send';
import Button from '@mui/material/Button';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

const AddItemForm: React.FC = () => {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');
  const dispatch = useDispatch();

  useEffect(() => {
    // FIX: Prevent Quill from using deprecated event listeners
    const observer = new MutationObserver(() => {}); 
    const config = { childList: true, subtree: true };

    document.querySelectorAll('.ql-editor').forEach((editor) => {
      observer.observe(editor, config);
    });

    return () => observer.disconnect();
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(addItem({ question, answer }));
    setQuestion('');
    setAnswer('');
  };

  return (
    <Box component="div">
      <h2>Add Item</h2>
      <form onSubmit={handleSubmit}>
        <TextField
          fullWidth
          label="Question"
          variant="outlined"
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          margin="normal"
        />
        
        {typeof window !== "undefined" && (
          <ReactQuill
          value={answer}
          onChange={setAnswer}
          modules={{
            toolbar: [
              [{ header: '1' }, { header: '2' }, { font: [] }],
              [{ list: 'ordered' }, { list: 'bullet' }],
              ['bold', 'italic', 'underline'],
              [{ align: [] }],
              ['link'],
              [{ size: ['small', 'medium', 'large', 'huge'] }],
              [{ color: [] }, { background: [] }],
              ['blockquote', 'code-block'],
              ['clean'],
            ],
          }}
          formats={['header', 'font', 'size', 'bold', 'italic', 'underline', 'list', 'link', 'align', 'color', 'background', 'blockquote', 'code-block']}
          style={{ height: '300px' }}
        />)
        }

        <Button type="submit" variant="contained" endIcon={<SendIcon />}>
          Add
        </Button>
      </form>
    </Box>
  );
};

export default AddItemForm;
